<template>
  <div class="row fadeInLeft">
    <div class="bookwall" >
      <div class="column col-sm-6 col-md-4" v-for ="(book,index) in this.books">
        <BookCard :isLogin="isLogin" :id="book.id" :imagePath="book.imagePath" :name="book.bookname" :price="book.price" :author="book.author" :introduction="book.introduction" :defaultAddress="currentUser.defaultAddress" :saleAmount="book.saleAmount" :amount="book.amount"/>
      </div>
      <!--
      <div class="column col-sm-6 col-md-3">
        <BookCard v-for ="(book,index) in this.books" :isLogin="isLogin" :imagePath="book.imagePath" :name="book.bookname" :price="book.price" :author="book.author" :introduction="book.introduction" :defaultAddress="currentUser.defaultAddress" :saleAmount="book.saleAmount"/>
      </div>
      <div class="column col-sm-6 col-md-3">
        <BookCard v-for ="(book,index) in this.books" :isLogin="isLogin" :imagePath="book.imagePath" :name="book.bookname" :price="book.price" :author="book.author" :introduction="book.introduction" :defaultAddress="currentUser.defaultAddress" :saleAmount="book.saleAmount"/>
      </div>
      <div class="column col-sm-6 col-md-3">
        <BookCard v-for ="(book,index) in this.books" :isLogin="isLogin" :imagePath="book.imagePath" :name="book.bookname" :price="book.price" :author="book.author" :introduction="book.introduction" :defaultAddress="currentUser.defaultAddress" :saleAmount="book.saleAmount"/>
      </div>-->
    </div><!-- /.reviews -->
  </div>
</template>

<script >
import BookCard from './BookCard.vue'
export default {
  name: 'BookWall',
  data () {
      return {
        books:[],
        isLogin:false,
        currentUser: {
        },
      }
  },
  mounted(){
    let that = this;
    $.ajax({
      type:"get",
      url:"/profile",
      success:function(data){
        if(data==="false"){
          that.isLogin = false
        }else{
          that.isLogin = true;
          that.currentUser = JSON.parse(data);
        }
      }
    });
    $.ajax({
      type:"get",
      url:"/books",
      success:function(data){
        that.books = JSON.parse(data);
      },
      error:function () {
        alert("服务器错误,获取图书信息失败!");
      }
    })
  },
  components:{BookCard}
}

</script>

<style >
.bookwall{
  padding-left: 40px;
  padding-right: 40px;
}
  .bookwall .column{
    padding-left: 20px;
    padding-right: 20px;
  }

</style>



